<template>
    <div class="main-page">
        <div class="echarts-box"></div>
        <div class="table-box">
            <table border>
                <tr>
                    <th>仓库名称</th>
                    <th>数量</th>
                </tr>
                <tr v-for="(item,index) in tableData" :key="index">
                    <th style="text-align:left;padding:15px">{{item.WarehouseName}}</th>
                    <th style="text-align:right;padding:15px">{{item.Qty}}</th>
                </tr>
            </table>
        </div>
    </div>
</template>

<script>
export default {
    name: "Resources",
    props: {},
    components: {},
    data() {
        return {
            tableData: []
        }
    },
    methods: {
        start() {
            let ele = document.getElementsByClassName("echarts-box")[0]
            this.$echarts.init(ele).dispose()
            let myChart = this.$echarts.init(ele)
            myChart.setOption(
                {
                    xAxis: {
                        type: 'category',
                        data: this.tableData.map(it => it.WarehouseName),
                        axisLabel: {
                            rotate: 40
                        }
                    },
                    yAxis: {
                        type: 'value'
                    },
                    series: [
                        {
                            data: this.tableData.map(it => it.Qty),
                            type: 'bar',
                            itemStyle: {
                                normal: {
                                    label: {
                                        show: true,
                                        position: "top"
                                    }
                                }
                            }
                        }
                    ]
                }
            )
        },
        OSMS_InventoryIndex() {
            return this._api.OSMS_InventoryIndex(this.$route.query).then(res => {
                if (res) {
                    this.tableData = res.data
                }
            })
        }
    },
    async mounted() {
        await this.OSMS_InventoryIndex()
        this.start()
    }
}
</script>

<style lang="scss" scoped>
.main-page {
    // background: #272755;

    .echarts-box {
        width: 100%;
        height: 50%;
    }

    .table-box {
        width: 100%;
        height: 50%;
        padding: 15px;
    }

    table {
        width: 100%;
        height: calc(100% - 100px);
        text-align: center;
        border: 1px solid #bbb;
        border-collapse: collapse;
    }
    td {
        width: 120px;
        padding: 0 10px;
        border: 1px solid #bbb;
        box-sizing: border-box;
        text-align: center;
    }
    tr {
        height: 30px;
        color: #444444;
    }
}
</style>